import React from 'react'
import { Link } from 'react-router-dom'

import { makeStyles } from '@material-ui/core/styles';
import { ListItem, Divider, ListItemText, ListItemAvatar, Avatar, Typography, ListItemSecondaryAction, IconButton, ButtonBase } from '@material-ui/core';

import MoreHoriz from '@material-ui/icons/MoreHoriz'
import Bookmark from '@material-ui/icons/Bookmark'
import BookmarkBorder from '@material-ui/icons/BookmarkBorder'

const useStyles = makeStyles((theme) => ({
    root: {
      width: '100%',
      //maxWidth: '36ch',
      backgroundColor: theme.palette.background.paper,
    },
    inline: {
      display: 'inline',
    },
    buttonText:{
        textAlign:'left'
    }
  }));


const UnitListItem = ({unit, onToggle}) => {
    const classes = useStyles();

    const toggleBookmark = () =>{

    }
    
    return (
            <ListItem alignItems="flex-start" ContainerComponent='div'>
                {/* <ButtonBase className={classes.buttonText} onClick={() => { alert(unit.name) }}> */}
                <Link to={{pathname:'/details', units:{unit}, func:{onToggle}}} style={{ textDecoration: 'none', color:'black', display:'flex', flexDirection:'row' }}>
                <ListItemAvatar>
                    <Avatar alt={unit.name} src={unit.image} />
                </ListItemAvatar>
                <ListItemText
                    primary={unit.name}
                    secondary={
                        <React.Fragment>
                            <Typography
                                component="span"
                                variant="body2"
                                className={classes.inline}
                                color="textPrimary"
                            >
                                {unit.class}
                            </Typography>
                                {" — " + unit.desc}
                        </React.Fragment>
                    }
                />
                </Link>
        {/* </ButtonBase> */}

                <ListItemSecondaryAction onClick={() => onToggle(unit.id)}>
                    <IconButton edge="end" aria-label="comments">
                        {unit.bookmarked? <BookmarkBorder /> : <Bookmark /> }
                    </IconButton>
                </ListItemSecondaryAction>
            </ListItem>
    )
}

export default UnitListItem
