import { List } from '@material-ui/core';
import { useState } from 'react'

import UnitListItem from "./UnitListItem";
import { makeStyles } from '@material-ui/core/styles';
import Pagination from '@material-ui/lab/Pagination';

import { ListItem, Divider, ListItemText, ListItemAvatar, Avatar, Typography, ListItemSecondaryAction, IconButton, ButtonBase } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
      width: '100%',
      //maxWidth: '50%',
      //minHeight:'300px',
      backgroundColor: theme.palette.background.paper,
    },
    inline: {
      display: 'inline',
    },
  }));

const UnitList = ({units, onToggle}) => {
    const classes = useStyles();

    const itemsPerPage = 4;
    const [page, setPage] = useState(1)
    const [numOfPages] = useState(Math.ceil(units.length / itemsPerPage))
    const handlePaging = (event, page) => {
      setPage(page);
      console.log(page);
    }

    return (
        <>
        <List className={classes.root}>
            {units.slice((page - 1) * itemsPerPage, page * itemsPerPage).map((unit) => (<UnitListItem key={unit.id} unit={unit} onToggle={onToggle} />))}
            {/* {units.map((unit) => (
                <UnitListItem key={unit.id} unit={unit} onToggle={onToggle} />
            ))} */}
        </List>
        <Pagination count={numOfPages} page={page} onChange={handlePaging} showFirstButton showLastButton />
        </>
    )
}

export default UnitList
